import ajax from '@/services/ajax';
import api from '@/services/api';

export default {
    // 
    getTransactionList: data => {
        return ajax.post(api.getTransactionList, data);
    },
    // 
    getTransactionDetail: data => {
        return ajax.post(api.getTransactionDetail, data);
    }
};



