<template>
    <div>
        <div class="field-part">
            <mt-cell title="" @click.native="showAccount()" v-model="dataForm.account" isLink></mt-cell>
            <mt-cell title=":" v-model="dataForm.balance"></mt-cell>
            <mt-field label=":" placeholder="100" v-model="dataForm.order_money" type="number"></mt-field>
            <mt-field label=":" v-model="dataForm.password" :attr="{ maxlength: 6 }" type="text"></mt-field>
            <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount "></selector-payment-account>
        </div>
        <div class="field-action">
            <mt-button type="primary" size="small" @click="$router.go(-1)"></mt-button>
            <mt-button type="primary" size="small" @click="order"></mt-button>
        </div>
        <div class="field-warn">
            <p></p>
            <p>1ATM</p>
            <p>210010005000</p>
            <p>330</p>
            <p>4</p>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                dataForm: {
                    account: '',
                    balance: '',
                    order_money: '',
                    password: ''
                },
                selectorPaymentAccount: {
                    checked: {},
                    showAddBtn: true,
                    showWarn: true,
                    accountList: [
                        {
                            id: 4,
                            name: '',
                            alias: '',
                            account: '625228***0089',
                            bankName: '',
                            balance: '200000.66'
                        },
                        {
                            id: 5,
                            name: '2',
                            alias: '2',
                            account: '625228***0025',
                            bankName: '',
                            balance: '5000.27'
                        },
                        {
                            id: 6,
                            name: '3',
                            alias: '',
                            account: '625228***0109',
                            bankName: '',
                            balance: '600.34'
                        }
                    ]
                }
            };
        },
        methods: {
            showAccount() {
                this.$refs.selectorPaymentAccount.show().then((data) => {
                    this.selectorPaymentAccount.checked = data.item;
                    this.dataForm.account = this.selectorPaymentAccount.checked.account;
                    this.dataForm.balance = this.selectorPaymentAccount.checked.balance + ' ';
                }, (data) => {
                });
            },
            order() {
                let _data = Object.assign({}, this.dataForm);
                let money = parseFloat(_data.order_money) % 100;
                if (this.$validate.isEmpty(_data.account)) {
                    this.doMsg('');
                    return false;
                }
                if (this.$validate.isEmpty(_data.order_money)) {
                    this.doMsg('');
                    return false;
                }
                if (money !== 0) {
                    this.doMsg('100');
                    return false;
                }
                if (this.$validate.isEmpty(_data.password)) {
                    this.doMsg('');
                    return false;
                }
                if (!this.$validate.isPwd(_data.password)) {
                    this.doMsg('');
                    return false;
                }
                this.$router.push({ name: 'noCardResult', query: { p: -2 } });
            },
            doMsg(msg, cl, duration) {
                this.$toast(msg);
            }
        }
    };

</script>


